const LIBRARY_KEY = "myLibrary";

// **K√ºt√ºphanedeki Filmleri Getir**
export function getLibraryMovies() {
    return JSON.parse(localStorage.getItem(LIBRARY_KEY)) || [];
}

// **Filmi K√ºt√ºphaneye Ekle**
export function addMovieToLibrary(movie) {
    console.log(`üìå Film ekleniyor: ${movie.title}`);
    
    let movies = getLibraryMovies();
    if (!movies.some(m => m.id === movie.id)) {
        movies.push(movie);
        localStorage.setItem("myLibrary", JSON.stringify(movies));
        
        console.log("‚úÖ Film ba≈üarƒ±yla eklendi!", getLibraryMovies()); // ‚úÖ **Test Logu**
        Swal.fire({
            title: `${movie.title} Added to Library! üé¨`,
            icon: "success",
            confirmButtonText: "OK",
            confirmButtonColor: "#ff9800", // üçä Turuncu buton
            timer: 2500, // ‚è≥ 2.5 saniye sonra kapanƒ±r
            toast: true,
            showCloseButton: true,
        });

        // üìå **My Library Sayfasƒ±nƒ± G√ºncelle**
        if (window.location.pathname.includes("mylibrary.html")) {
            displayLibraryMovies();
        }
    } else {
        console.log("‚ö†Ô∏è Film zaten k√ºt√ºphanede!", getLibraryMovies());
        alert(`${movie.title} is already in My Library!`);
    }
}

// **Filmi K√ºt√ºphaneden Sil**
export function removeMovieFromLibrary(movieId) {
    let movies = getLibraryMovies();
    const movie = movies.find(m => m.id === movieId); // Kaldƒ±rƒ±lacak filmi bul

    if (!movie) return; // Eƒüer film yoksa i≈ülem yapma

    movies = movies.filter(m => m.id !== movieId);
    localStorage.setItem(LIBRARY_KEY, JSON.stringify(movies));

    // ‚úÖ **SweetAlert ile kaldƒ±rma bildirimi**
    Swal.fire({
    
        text: `${movie.title} has been removed from your library.`,
        icon: "error",
        timer: 2000,
        showConfirmButton: false,
    });

    // üìå **Library Sayfasƒ±nƒ± G√ºncelle**
    if (window.location.pathname.includes("mylibrary.html")) {
        displayLibraryMovies();
    }
}


// **Film K√ºt√ºphanede Var mƒ±?**
export function isMovieInLibrary(movieId) {
    let movies = getLibraryMovies();
    return movies.some(m => m.id === movieId);
}

// **Film K√ºt√ºphaneye Eklendi Mi? Toggle ƒ∞≈ülemi**
export function toggleLibrary(movie) {
    let movies = getLibraryMovies();
    if (movies.some(m => m.id === movie.id)) {
        removeMovieFromLibrary(movie.id);
        return "Add to Library";
    } else {
        addMovieToLibrary(movie);
        return "Remove from Library";
    }
}
